import mongoose from 'mongoose'
import Review from './Review'

const { Schema } = mongoose

const Picture = new Schema({
  name: {
    type: String,
    required: true,
  },
  path: {
    type: String,
    required: true,
  },
})

const Product = new Schema({
  name: {
    type: String,
    required: true,
  },
  category: {
    type: String,
    required: true,
  },
  stock: {
    type: Number,
    required: true,
  },
  price: {
    type: Number,
    required: true,
  },
  thumnail: [Picture],
  pictures: [Picture],
  promotions: [{
    name: String,
    period: String,
  }],
  information: {
    type: String,
    required: true,
  },
  reviews: [Review],
  related: [{
    name: { 
      type: String, 
      required: true 
    },
    price: { 
      type: Number, 
      required: true 
    },
    thumnail: [Picture],
  }],
  status: {
    type: String,
    enum: ['NEW', 'HOT', 'SALE', null],
    default: null,
  }
}, { timestamps: true })

export default mongoose.model('Product', Product)